import React, { Component } from 'react';

var google_api_key = 'AIzaSyCj9g81Vz2y1bKbiOHPICiEH6nZDJ7DRjo';

class PlacePhotos extends Component{
    constructor(props){
        super(props);
        this.state = {
            photoSrc: []
        }
    }
    
    componentWillReceiveProps(nextProps){
        var photos = nextProps.place.photos;
        
        var src = '';
        var arr = [];
        if(photos){
            photos.forEach(element => {
                src = 'https://maps.googleapis.com/maps/api/place/photo?maxwidth=400&photoreference=' + element.photo_reference + '&sensor=false&key=' + google_api_key;
                arr.push(src);
            });
            this.setState({photoSrc: arr});
        }else{
            this.setState({photoSrc: []});
        }
        
    }

    render(){
        
        return(
            <div >
               <h3 className="mainHeader"> Image Gallery: </h3>
                <div className="gallery">
                {
                    this.state.photoSrc.map(function(data, index){
                        return (
                            <img src = {data} alt="data" key={index} className="rounded mx-auto d-block" />
                        )
                    })
                }
                </div>

            </div>
        )
    }
}

export default PlacePhotos;


