import * as types from './actionTypes';
import apiCalls from '../services/apicalls';

export function GetPlacesList(query){
    return async(dispatch, getState) =>{
        try{
                let placeDetails = await apiCalls.GetPlacesList(query);
                if(placeDetails){
                    
                    var placeList = placeDetails.results;
                    dispatch({type: types.PLACE_LIST, placeList});
                }
        }
        catch(error){
            console.log(error);
        }
    }
}

export function GetPlaceDetail(placeId){
    return async(dispatch, getState) =>{
        try{
                let detail = await apiCalls.GetPlaceDetail(placeId);
                if(detail){
                    var placeDetail = detail.result;
                    dispatch({type: types.PLACE_DETAIL, placeDetail});
                }
        }
        catch(error){
            console.log(error);
        }
    }
}


