var google_api_key = 'AIzaSyCj9g81Vz2y1bKbiOHPICiEH6nZDJ7DRjo';

class apiCalls {
    GetPlacesList(query) {
        const url = 'https://maps.googleapis.com/maps/api/place/textsearch/json?query=' + query + '&key=' + google_api_key;

        return fetch(url, {
            method: 'GET',
            mode: 'cors',
            headers: {
                //Accept: 'application/jsonp',
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Credentials':true,
                'Access-Control-Allow-Methods':'POST, GET'
            }
        }).then(function(response) {
            return response.json();

        }).catch(function(error) {
            throw new Error(error);
        });
    }

    GetPlaceDetail(placeId){
        const url = 'https://maps.googleapis.com/maps/api/place/details/json?placeid=' + placeId + '&key=' + google_api_key;
        return fetch(url, {
            method: 'GET',
            mode: 'cors',
            headers: {
                //Accept: 'application/jsonp',
                'Access-Control-Allow-Origin': '*',
                'Access-Control-Allow-Credentials':true,
                'Access-Control-Allow-Methods':'POST, GET'
            }
        }).then(function(response) {
            return response.json();

        }).catch(function(error) {
            throw new Error(error);
        });
    }
}


export default new apiCalls();


